/* IBM Icons */

/* svg icons */

// Fixes: https://github.ibm.com/WatsonTooling/ibmwatson-common-ui/issues/163
svg use {
  pointer-events : none;
}

svg.ibm-icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  fill: color('cool-gray', 80);


  &.small {
    width: 16px;
    height: 16px;
  }

  &.large {
    width: 32px;
    height: 32px;
  }
}

.dark-theme {
  svg.ibm-icon {
    fill: color('cool-gray', 20);

  }
}

/* font-based icons */

[class^="ibm-icon--"] {
    font-family: 'ibm-icons';
    speak: none;
    font-size: 1.5em;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &::before {
        vertical-align: middle;
    }

    &.small {
      font-size: 1em;
    }

    &.large {
      font-size: 2em;
    }
}
